.listview {
    display: block;
    position: relative;
    min-height: 120px;
    margin-bottom: 8px;
    background-color: #ffffff;
    padding: 16px;
    border-radius: 16px;
}

.listview ol {
    margin: 0;
    padding-left: 2em;
}

.listview ol li {
    clear: both;
    margin-bottom: 8px;
}

.listview ol li span{
    float: left;
    font-size: 16px;
    position: relative;
}

.listview ol li span::after {
   content: "";
   position: absolute;
   left: 0;
   top: 50%;
   transition: width 0.s ease-in-out;
   width: 0%;
   height: 5px;
   background-color: #524FA1;
}

.listview ol li.completed span::after {
   width: 100%;
}

/* .listview ol li.completed span {
    text-decoration: line-through;
} */

.listview ol li button {
    float: right;
    margin-left: 4px;
    cursor: pointer;
    border: none;
    color: #ffffff;
    background-color: #524FA1;
    padding: 4px 8px;
}

.listview ol li button.remove {
    background-color: #FA466A;
}